import React, {useState, useEffect} from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import {LazyLoadImage} from 'react-lazy-load-image-component';
import 'react-lazy-load-image-component/src/effects/blur.css';

function ImageGridDisplay(props) {
    const [currentIndex, setCurrentIndex] = useState(0);
    const {images = [], modalId = "imageModal"} = props;

    // New state for processed images and loading status
    const [processedImages, setProcessedImages] = useState([]);
    const [isLoading, setIsLoading] = useState(true);

    const imageStyle = {
        padding: '5px',
        borderRadius: '10px',
        width: '100%',
        height: '100%',
        objectFit: 'cover'
    };

    // --- New useEffect to determine image orientation ---
    useEffect(() => {
        const fetchImageOrientations = async () => {
            if (!images || images.length === 0) {
                setIsLoading(false);
                return;
            }

            setIsLoading(true);
            const imagePromises = images.map(image => {
                return new Promise((resolve) => {
                    const img = new Image();
                    img.src = image.src;
                    img.onload = () => {
                        resolve({
                            ...image,
                            orientation: img.naturalWidth > img.naturalHeight ? 'landscape' : 'portrait'
                        });
                    };
                    img.onerror = () => {
                        // If an image fails to load, default to landscape
                        resolve({...image, orientation: 'landscape'});
                    };
                });
            });

            const imagesWithOrientations = await Promise.all(imagePromises);
            setProcessedImages(imagesWithOrientations);
            setIsLoading(false);
        };

        fetchImageOrientations();
    }, [images]); // Rerun this effect if the images prop ever changes

    const openModal = (index) => setCurrentIndex(index);
    const closeModal = () => setCurrentIndex(null);

    // This layout logic is the same as before, but uses `processedImages` state
    // This layout logic is the same as before, but uses `processedImages` state
    const generateLayout = () => {
        const rows = [];
        let i = 0;
        while (i < processedImages.length) {
            const currentImage = processedImages[i];
            const nextImage = processedImages[i + 1];

            if (currentImage.orientation === 'landscape') {
                const index = i; // capture the index
                rows.push(
                    <div className="row mb-3" key={`row-${index}`}>
                        <div className="col-12">
                            <LazyLoadImage
                                style={imageStyle}
                                src={currentImage.src}
                                alt={currentImage.title}
                                onClick={() => openModal(index)}
                                data-bs-toggle="modal"
                                data-bs-target={`#${modalId}`}
                                effect="blur"
                            />
                        </div>
                    </div>
                );
                i++;
            } else if (nextImage && nextImage.orientation === 'portrait') {
                const index1 = i;
                const index2 = i + 1;
                rows.push(
                    <div className="row mb-3" key={`row-${index1}`}>
                        <div className="col-6">
                            <LazyLoadImage
                                style={imageStyle}
                                src={currentImage.src}
                                alt={currentImage.title}
                                onClick={() => openModal(index1)}
                                data-bs-toggle="modal"
                                data-bs-target={`#${modalId}`}
                                effect="blur"
                            />
                        </div>
                        <div className="col-6">
                            <LazyLoadImage
                                style={imageStyle}
                                src={nextImage.src}
                                alt={nextImage.title}
                                onClick={() => openModal(index2)}
                                data-bs-toggle="modal"
                                data-bs-target={`#${modalId}`}
                                effect="blur"
                            />
                        </div>
                    </div>
                );
                i += 2;
            } else {
                const index = i;
                rows.push(
                    <div className="row mb-3" key={`row-${index}`}>
                        <div className="col-12">
                            <LazyLoadImage
                                style={imageStyle}
                                src={currentImage.src}
                                alt={currentImage.title}
                                onClick={() => openModal(index)}
                                data-bs-toggle="modal"
                                data-bs-target={`#${modalId}`}
                                effect="blur"
                            />
                        </div>
                    </div>
                );
                i++;
            }
        }

        return rows;
    };

    // --- Show a loading spinner while processing ---
    if (isLoading) {
        return (
            <div className="d-flex justify-content-center align-items-center" style={{minHeight: '300px'}}>
                <div className="spinner-border" role="status">
                    <span className="visually-hidden">Loading...</span>
                </div>
            </div>
        );
    }

    return (
        <div className="container">
            {generateLayout()}

            {/* Modal remains the same */}
            <div className="modal fade" id={modalId} tabIndex="-1" aria-hidden="true">
                <div className="modal-dialog modal-dialog-centered modal-lg">
                    <div className="modal-content">
                        <div className="modal-header">
                            <h5 className="modal-title">
                                {currentIndex !== null && processedImages[currentIndex]?.title}
                            </h5>
                            <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"
                                    onClick={closeModal}></button>
                        </div>
                        <div className="modal-body text-center">
                            {currentIndex !== null && (
                                <LazyLoadImage src={processedImages[currentIndex]?.src} className="img-fluid"
                                               alt="Selected" effect="blur"/>
                            )}
                        </div>
                        <div className="modal-footer">
                            <button type="button" className="btn btn-secondary"
                                    onClick={() => setCurrentIndex(prev => (prev > 0 ? prev - 1 : processedImages.length - 1))}
                                    disabled={processedImages.length <= 1}>
                                Previous
                            </button>
                            <button type="button" className="btn btn-primary"
                                    onClick={() => setCurrentIndex(prev => (prev < processedImages.length - 1 ? prev + 1 : 0))}
                                    disabled={processedImages.length <= 1}>
                                Next
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default ImageGridDisplay;