import React, {useState} from "react";
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.bundle.min.js';
import Speeches from "./Speeches";
import Ceremony from "./Ceremony";
import Couple from "./Couple";
import Highlights from "./Highlights";
import GroupPortraits from "./GroupPortraits";
import FirstDances from "./FirstDances";
import PhotosNavBar from "../../components/PhotosNavBar";
import './WeddingPhotos.css';

function WeddingPhotos() {
    const [activeTab, setActiveTab] = useState("ceremony");

    const handleTabClick = (tabName) => {
        setActiveTab(tabName);
    };

    const navStyle = {
        position: 'sticky',
        top: 0,
        width: '100%',
        zIndex: 1000,
        whiteSpace: 'nowrap' // Ensures menu items are on a single line
    };

    const navLinkStyle = {
        cursor: 'pointer',
        position: 'relative',
        paddingLeft: '10px',
        paddingRight: '10px'
    };

    const navLinkAfterStyle = {
        content: "'|'",
        position: 'absolute',
        right: '-10px',
        top: '0',
        bottom: '0',
        display: 'flex',
        alignItems: 'center'
    };

    return (
        <div style={{backgroundColor: '#E6E5DC'}}>
            {/*<PhotosNavBar handleTabClick={handleTabClick}/>*/}
            <div style={{paddingTop: '10px'}} className="text-center mb-2">
                <h1 className="mb-0">Wedding Gallery</h1>
                <p className="mb-0">Photos by <a href="https://rachelrossphotography.co.uk/">Rachel Ross Photography</a>
                </p>
            </div>


            <ul className="nav nav-tabs flex-nowrap overflow-x-auto overflow-y-hidden hide-scrollbar">
                <li className=" nav-item text-nowrap">
                    <a className={activeTab === 'ceremony' ? 'nav-link active' : 'nav-link'}
                        style={{cursor: "pointer"}}
                       onClick={(e) => { e.preventDefault(); handleTabClick('ceremony'); }}>
                        Ceremony
                    </a>
                </li>
                <li className=" nav-item text-nowrap">
                    <a className={activeTab === 'speeches' ? 'nav-link active' : 'nav-link'}
                       style={{cursor: "pointer"}}
                       onClick={(e) => { e.preventDefault(); handleTabClick('speeches'); }}>
                        Speeches
                    </a>
                </li>
                {/* ... apply the same pattern to the rest of your nav items ... */}
                <li className=" nav-item text-nowrap">
                    <a className={activeTab === 'couple' ? 'nav-link active' : 'nav-link'}
                       style={{cursor: "pointer"}}
                       onClick={(e) => { e.preventDefault(); handleTabClick('couple'); }}>
                        Just Us
                    </a>
                </li>
                <li className=" nav-item text-nowrap">
                    <a className={activeTab === 'highlights' ? 'nav-link active' : 'nav-link'}
                       style={{cursor: "pointer"}}
                       onClick={(e) => { e.preventDefault(); handleTabClick('highlights'); }}>
                        Highlights
                    </a>
                </li>
                <li className=" nav-item text-nowrap">

                    <a className={activeTab === 'groupportraits' ? 'nav-link active' : 'nav-link'}
                       style={{cursor: "pointer"}}
                       onClick={(e) => { e.preventDefault(); handleTabClick('groupportraits'); }}>
                        Group Portraits
                    </a>
                </li>
                <li className="nav-item text-nowrap">
                    <a className={activeTab === 'dances' ? 'nav-link active' : 'nav-link'}
                       style={{cursor: "pointer"}}
                       href="#dances"
                       onClick={(e) => { e.preventDefault(); handleTabClick('dances'); }}>
                        First Dance
                    </a>
                </li>
            </ul>

            <div style={{paddingTop: '15px'}} className="mt-3">
                {activeTab === 'ceremony' && <Ceremony/>}
                {activeTab === 'speeches' && <Speeches/>}
                {activeTab === 'couple' && <Couple/>}
                {activeTab === 'highlights' && <Highlights/>}
                {activeTab === 'groupportraits' && <GroupPortraits/>}
                {activeTab === 'dances' && <FirstDances/>}
            </div>

        </div>
    );
}

export default WeddingPhotos;