import Section from "../components/Section";
import NavBar from "../components/NavBar";
import Gifts from "./Gifts";
import GettingThere from "./GettingThere";
import WeddingDetails from "./WeddingDetails";
import Gallery from "./Gallery";
import Home from "./Home";
import ContactDetails from "./ContactDetails";
import WeddingPhotosSection from "./WeddingPhotosSection";
import {useEffect, useState} from "react";
import confetti from "canvas-confetti";

function HomePage() {


    const [hasLaunchedConfetti, setHasLaunchedConfetti] = useState(false);

    useEffect(() => {
        const timer = setTimeout(() => {

            // Check if the countdown has reached zero
            if (!hasLaunchedConfetti) {
                setHasLaunchedConfetti(true); // Set confetti launched flag

                // Launch confetti repeatedly for an extended celebration effect
                const confettiInterval = setInterval(() => {
                    confetti({
                        particleCount: 20,
                        angle: 60,
                        spread: 55,
                        origin: { x: 0 },
                        shapes: ["circle"], // Use circle to approximate heart shape
                        colors: ["#ff69b4", "#ff1493"], // Pink shades for a love theme
                    });
                    confetti({
                        particleCount: 40,
                        angle: 120,
                        spread: 45,
                        origin: { x: 1 },
                        shapes: ["circle"],
                        colors: ["#ff69b4", "#ff1493"],
                    });
                }, 250); // Every 250ms to create a longer, more constant flow of confetti

                // Stop confetti after 5 seconds
                setTimeout(() => {
                    clearInterval(confettiInterval);
                }, 5000);
            }
        }, 1000);

        // Clear the interval on component unmount
        return () => clearTimeout(timer);
    }, [hasLaunchedConfetti]);


    return (
        <div style={{backgroundColor: '#E6E5DC'}}>
            <NavBar/>
            <div className={'mainContent'}>
                <Section id="home" content={<Home/>}/>
                {/*<Section id="gallery" content={<Gallery/>}/>*/}
                <Section id="wedPhotos" content={<WeddingPhotosSection/>}/>
                <Section id="wedding" content={<WeddingDetails/>}/>
                <Section id="getting-there" content={<GettingThere/>}/>
                <Section id="gifts" content={<Gifts/>}/>
                {/*<Section id="rsvp" content={<RSVP/>}/>*/}
                <Section id="contact" content={<ContactDetails/>}/>
            </div>
        </div>
    );
}

export default HomePage;
