import React from "react";
import ImageGridDisplay from "../../components/ImageGridDisplay";
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.bundle.min.js';

function Speeches() {
    // Configure require.context to point to your wedding photos folder
    const imageContext = require.context('../../resources/weddingPhotos/speeches', false, /\.(png|jpe?g|gif)$/);

    // Get an array of image file paths
    const imageKeys = imageContext.keys();

    // Create an array of image objects with src and potentially a title
    const weddingImages = imageKeys.map(key => {
        // You can derive a title from the filename if needed
        const filename = key.replace('./', ''); // Remove the './' prefix
        // const title = filename.split('.')[0].replace(/[-_]/g, ' '); // Remove extension and replace hyphens/underscores with spaces

        return {
            src: imageContext(key),
            title: '' // Optional: Use the derived title or set a default
        };
    });

    return (
        <div className="container text-start">
            <ImageGridDisplay images={weddingImages} modalId="weddingPhotosModal" />
        </div>
    );
}

export default Speeches;